/*******************************************************************************
 * Themes
 ******************************************************************************/
 
:root {
 --global-bg-color: #{$white-color};
 --global-code-bg-color: #{$code-bg-color-dark};
 --global-text-color: #{$black-color};
 --global-text-color-light: #{$grey-color};
 --global-text-color-orange: #{$orange-color};
 --global-theme-color: #{$purple-color}; // Clickable links. initial was $purple-color
 --global-hover-color: #{$blue-color-dark}; // initial was $purple-color. Doesn't change anything??
 --global-footer-bg-color: #{$grey-color-dark};
 --global-footer-text-color: #{$grey-color-light};
 --global-footer-link-color: #{$white-color};
 --global-distill-app-color: #{$grey-color};
 --global-divider-color: rgba(0,0,0,.1);
 --global-card-bg-color: #{$white-color};
 --global-toc-bg-color: #{$white-color};
 --global-toc-txt-color: #{$muted-text-color};
 --global-toc-title-bg-color: #{$muted-text-color};
 --global-toc-title-txt-color: #{$white-color};
 --global-toc-border-color: #{$grey-color-light};

 .fa-sun {
 display : none;
 }
 .fa-moon {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }

 .repo-img-light {
 display: block;
 }
 .repo-img-dark {
 display: none;
 } position: absolute;
 top: 1em;
 margin-top: 1em;
}

html[data-theme='dark'] {
 --global-bg-color: #{$grey-color-dark};
 --global-code-bg-color: #{$code-bg-color-dark};
 --global-text-color: #{$grey-color-light};
 --global-text-color-light: #{$grey-color-light};
 --global-theme-color: #{$cyan-color};
 --global-hover-color: #{$cyan-color};
 --global-footer-bg-color: #{$grey-color-light};
 --global-footer-text-color: #{$grey-color-dark};
 --global-footer-link-color: #{$black-color};
 --global-distill-app-color: #{$grey-color-light};
 --global-divider-color: #424246;
 --global-card-bg-color: #{$grey-900};
 --global-toc-bg-color: #{$grey-color-dark};
 --global-toc-txt-color: #{$grey-color-light};
 --global-toc-title-bg-color: #{$muted-text-color};
 --global-toc-title-txt-color: #{$grey-color-light};
 --global-toc-border-color: #{$code-bg-color-dark};

 .fa-sun {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }
 .fa-moon {
 display : none;
 }

 .repo-img-light {
 display: none;
 }
 .repo-img-dark {
 display: block;
 }
}
